* {
  margin: 0;
  padding: 0;
}

html {
  width: 100%;
  margin: 0 Auto;
  text-align: center;
  font-family: "Roboto", sans-serif;
  background-color: #042033;
}

h1 {
  width: 90%;
  margin: 0 Auto;
  text-align: center;
  font-weight: 300;
  color: #deb992;
  padding-bottom: 20px;
}

h2{
    font-weight: 300;
    color: #deb992;
    padding-top: 20px;
    padding-bottom: 30px;
}

a {
  font-weight: 500;
  font-size: medium;
  text-decoration: underline;
  color: #869ba9;
  padding-bottom: 20px;
}

form{
    width: 50%;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    label{
        color:#869ba9;
        font-weight: 300;
        text-align: left;
        padding-bottom: 5px;
        padding-top: 15px;
        width: 100%;
    }
    input, select{
        height: 25px;
        padding-left: 10px;
        border-radius: 2px;
        background-color: #d4d4d4;
        border: none;
    }
    button{
        border-radius: 5px;
        background-color: #d4d4d4;
        height: 25px;
        margin-top: 10px;
    }
    .insideFomr{
        border: #869ba9 1px solid;
        display: flex;
        flex-direction: column;
        margin: 20px 0px 0px 0px;
        padding: 20px;
        position: relative;
        p{
            color:#869ba9;
            font-weight: 300;
            position: absolute;
            top: -11px;
            background-color: #042033;
            padding: 0px 10px;
        }
        input, select{
            width: 60%;
        }
        label:first-child{
            padding-top: 0px;
        }
    }
  
}

table{
    padding-top: 20px;
    width: 95%;
    min-width: 700px;
    margin: auto;
    thead{
        background-color: #869ba9;
        color: #042033;
    }
    tbody{
        background-color: #d4d4d4;
        color: #042033;

    }
}
.pagination{
    padding-top: 20px;
    width: 200px;
    display: flex;
    margin: auto;
    justify-content: space-around;
}
